@font-face {
  font-family: "Roboto";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: local("Roboto-regular"), url("../fonts/roboto-v29-latin_cyrillic-ext-regular.woff2") format("woff2"), url("../fonts/roboto-v29-latin_cyrillic-ext-regular.woff") format("woff"); }

@font-face {
  font-family: "Roboto";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: local("Roboto-bold"), url("../fonts/roboto-v29-latin_cyrillic-ext-700.woff2") format("woff2"), url("../fonts/roboto-v29-latin_cyrillic-ext-700.woff") format("woff"); }

*,
*::before,
*::after {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

.page {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

img {
  max-width: 100%;
  height: auto; }

.page-body {
  background-color: #ffffff;
  color: #000000;
  font-family: "Roboto", "Arial", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 30px;
  line-height: 30px; }

.header {
  background-image: url("../img/header.jpg");
  background-position: top center;
  background-repeat: no-repeat;
  background-size: cover; }

.header__wrapper {
  padding-top: 160px;
  padding-bottom: 100px; }

.header__wrapper h2 {
  font-weight: 700;
  font-size: 60px;
  line-height: 60px;
  color: #feed93;
  text-transform: uppercase;
  text-align: center;
  margin: 0;
  margin-bottom: 40px; }

.header__wrapper p {
  font-weight: 400;
  font-size: 48px;
  line-height: 48px;
  color: #feed93;
  text-align: center;
  margin: 0;
  padding: 0; }

.header__container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  width: 1075px;
  margin: 0 auto;
  margin-top: 90px; }

.header__container img {
  display: block;
  height: 555px; }

.header__text h3 {
  font-size: 40px;
  line-height: 40px;
  font-weight: 400;
  color: #ffffff;
  margin-bottom: 75px;
  margin-top: 60px; }

.header__text p {
  font-size: 21px;
  line-height: 21px;
  font-weight: 400;
  color: #ffffff;
  padding-left: 35px;
  text-align: left;
  width: 398px;
  margin: 0;
  margin-bottom: 30px; }

.header__text span {
  display: block;
  font-size: 21px;
  line-height: 21px;
  font-weight: 700;
  color: #ffffff;
  width: 500px;
  margin-top: 70px;
  margin-bottom: 80px; }

.header__button {
  display: block; }

.button {
  background-color: #efde00;
  color: #000000;
  font-size: 21px;
  line-height: 21px;
  font-weight: 700;
  width: 295px;
  padding: 20px 20px;
  border: none;
  border-radius: 25px; }
  .button:hover {
    background-color: rgba(239, 222, 0, 0.8); }
